'use strict'

import React, {
    Component,
    StyleSheet,
    View,
    Text,
    Navigator,
    Image,
    ScrollView,
    TouchableOpacity,
    Animated,
    Easing,
    Button,
    Toast,
} from 'qunar-react-native';

import ReactOrigin from 'react-native';

const styles = StyleSheet.create({
    operationContainer: {
        paddingBottom: 5,
        paddingHorizontal: 10,
        flexDirection: 'row',
    },
    operationText: {
        flex: 1,
        alignSelf: 'center',
    },
    operationTextHighlight: {
        color: '#1ba9ba',
    },
    itemText: {
        padding: 5,
        color: '#fff',
        alignSelf:'flex-end',
        fontSize: 32,
        fontWeight: 'bold'
    }
});

class ScrollViewExample extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            qrn: true,
            animated: true,
            data: getCoupleOfRandomColor(50),
        }
    }

    switch(prop) {
        this.setState({
            [prop]: !this.state[prop],
        })
    }

    render() {

        let ScrollViewComponent = (this.state.qrn ? ScrollView : ReactOrigin.ScrollView);
        let content = this.state.data.map((item, index)=>{
            return (
                <View style={{height: 50, backgroundColor: item}} key={index}>
                    <Text style={styles.itemText}>{index}</Text>
                </View>
            )
        })

        return (
            <View style={{flex:1, paddingTop: 5}}>
                <View style={styles.operationContainer}>
                    <Text style={styles.operationText}>scrollToscrollToonScrollAnimationEnd</Text>
                    <Button text='' onPress={() => this.refs.scrollView.scrollTo({y: Math.random()*2000, animated: this.state.animated})} />
                </View>
                <View style={styles.operationContainer}>
                    <Text style={styles.operationText}>scrollTo<Text style={styles.operationTextHighlight}>{this.state.animated ? '' : ''}</Text> onScrollAnimationEndonScrollAnimationEnd</Text>
                    <Button text='' onPress={() => this.switch('animated')} />
                </View>
                <ScrollViewComponent ref="scrollView"
                    onScrollAnimationEnd={()=>Toast.show('', 1000, Toast.BOTTOM)}>
                    {content}
                </ScrollViewComponent>
            </View>
        )
    }
}

function getCoupleOfRandomColor(num) {
    var colors = [];

    for(var i = 0; i < num; i++) {
        colors.push(getRandomColor());
    }

    return colors;
}

function getRandomColor() {
    var letters = '3456789ABC'.split('');
    var color = '#';
    for (var i = 0; i < 6; i++ ) {
        color += letters[Math.floor(Math.random() * 10)];
    }
    return color;
}

module.exports = {
    title: 'scrollTo',
    examples: [{
        render: () => {
            return (
                <ScrollViewExample />
            );
        },
    }]
};



